import React from 'react';
import styled from 'styled-components';
import { Title2, Banner, Button } from './homeStyledComponents';
import { colors } from '../styledComponents';

const Container = styled.div`
  margin: 0px 20px;
  padding: 20px 0px 40px;
  display: flex;
  flex-direction: row;
`;

const P = styled.p`
  color: ${colors.navy};
`;

const Column = styled.div`
  display: flex;
  flex-direction: column;
`;

const BannerContainer = styled.div`
  position: absolute;
  top: 90px;
`;

const LeftBlockContainer = styled.div`
  width: 100%;
  position: relative;
`;

const BeTilliste = () => (
  <Container id="BeTilliste">
    <LeftBlockContainer>
      <Title2>⋅ devenez tilliste ⋅</Title2>
      <BannerContainer>
        <Banner width="50vw" />
      </BannerContainer>
    </LeftBlockContainer>
    <Column>
      <P>
        {'Vous êtes diplômé dans le secteur de la couture ou vous avez cinq d\'expérience professionnelle,'}
        {'rejoignez notre équipe de tillistes'}
      </P>
      <P>⋅ ⋅ ⋅</P>
      <div>
        <Button style={{ margin: '0' }}>{'> je veux être tilliste <'}</Button>
      </div>
    </Column>
  </Container>
);

export default BeTilliste;



