import React from 'react';
import styled from 'styled-components';
import { Title2, Row } from './homeStyledComponents';
import NumerotedBlock from './NumerotedBlock';

const Container = styled.div`
  margin: 0px 20px;
  padding: 40px 0px 60px;
`;

const HowItWorks = () => (
  <Container id="HowItWorks">
    <Title2>⋅ comment ça marche ⋅</Title2>
    <Row>
      <NumerotedBlock number={1}>
        Vous commandez votre retouche sur tilli.fr ou sur notre application.
      </NumerotedBlock>
      <NumerotedBlock number={2}>
        {"Votre couturier se rend chez vous à l'heure de votre choix."}
      </NumerotedBlock>
      <NumerotedBlock number={3}>
        Une fois prête, votre retouche est livrée chez vous pour essayage.
      </NumerotedBlock>
    </Row>
  </Container>
);

export default HowItWorks;



