import React from 'react';
import styled from 'styled-components';
import Slider from 'react-slick';
import { Title2 } from './homeStyledComponents';
import { colors } from '../styledComponents';
import Feedback from './Feedback';
import photo1 from '../../assets/photo1.jpg';
import photo2 from '../../assets/photo2.jpg';
import photo3 from '../../assets/photo3.jpg';
import photo4 from '../../assets/photo4.jpg';
import photo5 from '../../assets/photo5.jpg';

const Container = styled.div`
  padding: 20px 0px 40px;
  background-color: ${colors.lightGrey};
`;

const feedbacks = [
  {
    id: '0',
    text: 'Super contact avec daphné. Etant PMR c\'est bien pratique d\'avoir une couturière creative à domicile pour concevoir et faire une ceinture de jeans qui se met d\'une seule main et un tablier protège cuisse et genoux pour les balades en fauteuil.',
    author: 'Didier D.',
  },
  {
    id: '6',
    image: photo1,
    text: 'Merci tilli pour ce moment !',
    author: 'Sandra R',
  },
  { id: '1', text: 'De très bons conseils, du travail de grande qualité et rapide, bravo !', author: 'Guillaume L.' },
  {
    id: '7',
    image: photo2,
    text: 'Ma super robe',
  },
  {
    id: '2',
    text: 'J\'avais un coussin d\'oreillette de casque à recoudre et 3 ourlets à faire. Ça a été fait en 1 semaine, une couturière très pro est tout passée chercher et me les a ramenés. Rien à redire sur le service, je recommande !',
    author: 'Marc P.',
  },
  {
    id: '8',
    image: photo5,
    author: 'Olivia P.N.',
    text: '',
  },
  {
    id: '3',
    text: 'Le concept est vraiment top et les prix très attractifs comparés aux tailleurs/retouches habituels ! Le grand plus est évidemment le fait que les couturières viennent chez toi, c\'est vraiment un gain de temps très appréciable ! Vive Tilli ! 😉',
    author: 'Miguel',
  },
  {
    id: '9',
    image: photo4,
    text: 'Hello',
  },
  {
    id: '4',
    text: 'Canon et c\'est jouissif de transformer des fringues qui hibernaient dans le placard !!!',
    author: 'Eleonore de R.',
  },
  {
    id: '10',
    image: photo3,
    text: 'Hello',
  },
  {
    id: '5',
    text: 'Brilliant service, pratique, rapide, top professionnal quality, équipe plus que compétente et sympa. Bravo Tilli !!!!! 20/20 !!!',
    author: 'Maria O.',
  },
];

const settings = {
  dots: true,
  infinite: true,
  slidesToShow: 4,
  slidesToScroll: 2,
  autoplay: true,
  autoplaySpeed: 3000,
  adaptiveHeight: true,
};

const TheyLoveTilli = () => (
  <Container>
    <Title2>⋅ ils ont aimé ⋅</Title2>
    <Slider {...settings}>
      {feedbacks.map(feedback =>
        <Feedback
          dataIndex={feedback.id}
          key={feedback.id}
          {...feedback}
        />,
      )}
    </Slider>
  </Container>
);

export default TheyLoveTilli;



